import { THIS_EXPR } from '@angular/compiler/src/output/output_ast';

export class Precio {
  [x: string]: any;
  labels: any;
  id: any;
  precio: number;
  producto: string;
  
    constructor(Precio) {
    this.producto = Precio.producto;
    this.precio = Precio.precio;
  }

  get mercancia() {
    let producto = '';

    if (this.producto && this.precio) {
      producto = this.producto + ' ' + this.precio;
    } else if (this.producto) {
      producto = this.producto;
    } 

    return producto;
  }

  set mercancia (value) {
  }


  
  
}
